.nonHeaderContent {
  padding: 30px 34px 14px;
  gap: 16px;
}

.checkboxRow {
  justify-content: flex-start;
  gap: 16px;
}

.artworkInstructions {
  justify-content: space-between;
  margin-top: 16px;
}

.filter {
  width: 100%;
  background: transparent;
  border: 0;
  outline: none;
}

.masonryWrapper {
  overflow-y: auto;
  overflow-x: hidden;
  height: calc(var(--maxDialogHeight) - 510px);
  min-height: 300px;
}

.searchWrapper {
  display: flex;
  border: 1px solid var(--color-SolidNeutralGray3);
  width: 100%;
  margin: 14px 0 24px;
}

.searchIconWrapper {
  display: flex;
  margin: 14px 10px 12px 18px;
}

.searchIconWrapper > .searchIconSVG {
  width: 15px;
  height: 15px;
  cursor: default;
}

@media (--mq-is-mobile) {
  .nonHeaderContent {
    padding: 24px;
    gap: 24px;
  }

  .searchWrapper {
    margin: 4px 0 24px;
  }

  .artworkInstructions {
    margin-top: 0;
  }
}

@media (--mq-is-not-mobile) {
  .categoryDialog {
    --maxDialogHeight: 90vh;
  }
}
