.idDigitalMediaVideo {
  width: 100%;
  object-fit: contain;
  margin: 0 auto;
  border-radius: 0;
  display: block;
}

.idDigitalMediaVideo[poster] {
  object-fit: contain;
  height: 100%;
}

.digitalMediaVideoWrapper {
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center;
}
