.idDigitalMediaItem {
    position: relative;
    background-color: var(--color-commonWhite);
    min-height: 100px;
}

.idDigitalMediaItem:hover > .bar {
    visibility: visible;
}

.playIcon {
    position: absolute;
    top: 0.875rem;
    right: 0.875rem;
}

.unlockableIconWrapper {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    top: 14px;
    left: 14px;
    width: 33px;
    height: 33px;
    background: rgba(0, 0, 0, 0.48);
    backdrop-filter: blur(12px);
    border-radius: 50%;
}

.unlockableIcon {
    position: relative;
    top: 0px;
    left: 0px;
}
