.tabPanelContainer {
  margin-top: 20px;
}

.categoryIconWrapper {
  display: inline-block;
  vertical-align: middle;
  width: 35px;
  height: 35px;
  background-color: var(--color-commonWhite);
  border: 1px solid var(--color-commonSelectedGray);
  border-radius: 9999px;
  cursor: pointer;
  margin-left: 8px;
}

.categoryIcon {
    width: 100%;
    height: 100%;
}

.createCategoryWrapper {
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  text-decoration: underline;
  margin-left: 28px;
  margin-top: 4px;
}

.createCategoryIcon {
  width: 35px;
  height: 35px;
  background-color: var(--color-commonWhite);
  border: 1px solid var(--color-commonSelectedGray);
  border-radius: 9999px;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}

.createCategoryWrapper > .createCategoryIcon > .createIconSVG {
  width: 12px;
  height: 12px;
}

.categoryTitle {
  line-height: 48px;
}

.tabsContainer {
  border-bottom: 1px solid var(--color-SolidNeutralGray1);
  display: flex;
}

[data-hascreatedcollected=true].tabsContainer {
  border-top: 1px solid var(--color-SolidNeutralGray1);
}

.deleteConfirmContent {
  gap: 24;
  display: flex;
  flex-direction: column;
  padding: 24px 34px;
}
