.barContent {
    padding: 2px;
    color: var(--color-textPrimary);
}


@media (--mq-is-not-mobile) {
    .bar {
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        flex-direction: column;
        justify-content: flex-end;
        pointer-events: none;
        visibility: hidden;
        display: flex;
    }

    .barContent {
        padding: 8px;
        background-color: rgba(0,0,0,0.2);
        color: var(--color-commonWhite);
    }
}

.editionInfo {
    display: flex;
    white-space: 'no-wrap';
}

.pricingInfo {
    border-top: 1px solid var(--color-commonWhite);
    display: flex;
    width: 100%;
    justify-content: space-between;
}

@media (--mq-is-mobile) {
    .pricingInfo {
        border-top: 1px solid var(--color-primaryP20);
    }
}

.pricingInfo:empty {
    border-top: 0;
}

.timerWrapper {
    display: inline-block;
    vertical-align: middle;
    line-height: 13px;
}

.header {
    display: flex;
    width: 100%;
    justify-content: space-between;
}

.rightAlignHack {
    margin-left: auto;
}
