.checkIcon {
  border-radius: 9999px;
  background-color: var(--color-commonWhite);
}

.digitalMediaItemWrapper {
  position: relative;
  cursor: pointer;
}

.digitalMediaItemWrapper .digitalMediaItem {
  min-height: 1px;
}

.mask {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  display: none;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  pointer-events: none;
  background-color: var(--color-primaryP45);
}

.mask[data-isselected="true"] {
  display: flex;
}
