.grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, 320px);
  justify-content: center;
  column-gap: 32px;
  row-gap: 32px;
  padding: 0 32px;
}

.filterRow {
  justify-content: flex-end;
  display: flex;
  padding: 32px 0 24px 0;
  flex-direction: row;
  gap: 20px;
}

.thumbnail {
  width: 56px;
  height: 56px;
  margin-right: 16px;
  border-radius: 2px;
  border: 1px solid transparent;
}

.activityTable {
  border-collapse: collapse;
  border-spacing: 0;
  width: 100%;
}

.tableXAxisScroll {
  overflow-x: overlay;
  width: 100%;
}

.tableCell {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-right: 12px;
}

.tableCellPadding {
  padding: 16px;
}

.columnHeader {
  padding: 0 16px 16px;
  text-align: left;
}

.colItem {
  max-width: 240px;
  min-width: 180px;
}

.colFrom {
  max-width: 300px;
  min-width: 150px;
}

.colTo {
  max-width: 300px;
  min-width: 150px;
}

.colEdition {
  max-width: 160px;
  min-width: 80px;
}

.colTime {
  max-width: 160px;
  min-width: 110px;
}

.colPrice {
  max-width: 160px;
  min-width: 80px;
}

.colAction {
  max-width: 160px;
  min-width: 80px;
}

.tableItem {
  /* Font */
  font-size: 17px;
  line-height: 20px;
  letter-spacing: -0.2px;
  color: var(--color-textPrimary);

  /* Layout */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.tableRow {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  width: '100%';
  align-items: center;
  justify-content: flext-start;
  border: none;
  border-bottom: none;
  margin-top: 0;
  margin-bottom: 0;
  pointer-events: '';
  overflow: '';
}

.tableColName {
  /* Font */
  font-size: 18px;
  font-family: var(--font-bold);
}

.textAnchor {
  font-family: var(--font-medium);
  font-size: 17px;
  color: var(--color-textPrimary);
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  };
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.textRegular {
  font-size: 17px;
}

.actionIcon {
  border: 'none';
  border-radius: '';
  cursor: 'default';
  height: '15px';
  object-fit: 'fill';
  pointer-events: 'all';
  width: '15px';
}

.nftGridMargin {
  margin-top: 24px;
}

.mobileActionImage {
  cursor: default;
  height: 10px;
  object-fit: fill;
  width: 10px;
}

.mobileActivityFeedItem {
  /* Font */
  padding: 12px;
  margin-bottom: 16px;

  /* Layout */
  background: var(--white);
  box-shadow: 0px 0px 6px 3px rgba(0, 0, 0, 0.05);
  border-radius: 6px;
}

.mobileActivityFeedItemTitle {
  /* Font */
  font-family: var(--font-bold);
  font-size: 18px;
  line-height: 20px;
  letter-spacing: -0.2px;
  color: var(--color-textPrimary);

  /* Layout */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.mobileActivityFeedItem p {
  /* Font */
  text-align: left;
  font-size: 12px;
  line-height: 12px;
  letter-spacing: -0.2px;
  color: var(--color-textPrimary);

  /* Layout */
  padding: 1.6px 0;
  margin: 0;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.mobileVerticalSeparator {
  width: 1px;
  margin: 0 12px 0 9.6px;
  border-right: 1.5px solid var(--color-SolidNeutralGray1);
}

.mobileTopInfo {
  font-family: var(--font-regular);
  font-size: 12px;
  margin-left: 4.8px;
  display: inline;
}

.mobileMediaTitle {
  margin: 5.6px 0;
}

.mobileHeader {
  font-family: var(--font-bold);
  display: inline;
}

.mobileValue {
  font-family: var(--font-regular), sans-serif;
}

.mobileThumbnailImage {
  width: 87px;
  height: 87px;
  margin-right: 16px;
  display: inline;
}

.mobileInvisAnchor {
  pointer-events: all;
  color: var(--color-textPrimary);
  text-decoration: none;
}

.mobileInvisAnchorNoTxtDecor {
  pointer-events: all;
  color: var(--color-textPrimary);
  text-decoration: none;
}

.mobileTitle {
  justify-content: 'flex-start';
  margin: 4px 0;
  max-width: 320px;

  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

@media (--mq-is-mobile) {
  .filterRow {
    padding: 20px 0;
    justify-content: center;
  }

  .nftGridMargin {
    margin-top: 0px;
  }
}

.activities {
  margin: 0 auto;

  @media screen and (min-width: 1920px) {
    max-width: var(--profile-maxWidth);
  }
}
