.bannerRoot {
  /* e.g. height would be calculated as width / aspect-ratio */
  /* 1920/(158+ 192)  */
  --aspectRatio: 5;
  /* percent from the top where we end our clip  */
  /* 158 / (158 + 192) */
  --startClip: 0.45143;
  /* 100vw / 5.4958143 * startClip(0.45143) */
  --startReverseClipInVW: 9.8vw;
  --startClipPercentage: 45.143%;
  --bannerHeight: 100% / var(--aspectRatio);
  /*  95 / 192  */
  --maskClipRatio: 0.48958333333;;
}

.base {
  background-color: var(--color-SolidNeutralGray5);
  background-size: 100% auto;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
}

.image {
  background-color: var(--color-SolidNeutralGray5);
  background-size: 100% auto;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  padding-top: calc(var(--bannerHeight));
  display: block;
}

@media (--mq-is-mobile) {
  .bannerRoot {
      --aspectRatio: 2.91;
  }

  .image {
      background-position: center;
      background-size: cover;
  }
}
