.idDigitalMediaImage {
    object-fit: contain;
    margin: 0 auto;
    display: block;
    width: 100%;
}

.digitalMediaEffect {
    margin: 0 auto;
    overflow: hidden;
    position: relative;
    background: linear-gradient(-90deg, #eee 0%, #f8f8f8 50%, #eee 100%);
    background-size: 400% 400%;
    width: 100%;
    animation: pulse 1.2s ease-in-out infinite;
}

@keyframes pulse {
    0% {
      background-position: 0% 0%;
    }
    100% {
      background-position: -135% 0%;
    }
}
